@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@300;400&family=Noto+Serif+JP:wght@400;600&display=swap);
:root {
  --width-tablet: 480px;
  --width-desktop: 768px;
  --red: #970000;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

script {
  display: none !important;
}

body {
  margin-left: auto;
  margin-right: auto;
  padding-top: 2rem;
  margin-bottom: 4rem;
}

section {
  margin-bottom: 4rem !important;
}

img {
  display: block;
  height: auto;
  width: 100%;
}

button {
  display: block;
  outline: none;
  border: none;
  background: var(--red);
  height: 50px;
  width: 100%;
  margin-top: 2rem !important;
}

button > a {
  color: #ffffff;
  text-decoration: none;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
  height: 100%;
}

svg {
  margin: 0 !important;
}

.rotate {
  position: fixed;
  height: 30px;
  width: 30px;
  top: 50%;
  left: 50%;
  animation: spin 3s linear infinite;
  -webkit-animation: spin 3s linear infinite;
}

@keyframes spin {
  100% {
    transform: rotate(360deg);
  }
}

@media (min-width: 768px) {
  body {
    padding-top: 4rem;
  }

  button {
    width: 180px;
    margin-left: auto;
    margin-right: auto;
  }
}

body * {
  display: block;
  font-family: "Noto Serif JP", serif;
  margin-top: 1.5em;
  margin-bottom: 0.7em;
}

p,
a,
nav-text {
  font-family: "Noto Sans JP", sans-serif;
}

a {
  font-size: 1.2rem;
  color: #000000;
}

p {
  display: block;
  font-weight: 300;
  font-size: 1.2rem;
  line-height: 1.6;
  margin-bottom: 1em;
}

q,
quote-author {
  font-size: 1.05rem;
  line-height: 1.5;
}

content-container {
  display: block;
  position: relative;
  margin-left: 2rem;
  margin-right: 2rem;
  margin-bottom: 120px;
}

page-title {
  font-size: 1.4rem;
  font-weight: 600;
}

section-title {
  font-size: 1.3rem;
  font-weight: 500;
}

quote-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
}

quote-container * {
  font-weight: 400;
}

quote-container q {
  margin-top: 0;
}

quote-author {
  margin-top: 0;
  margin-bottom: 0;
  margin-right: 2.5rem;
}

@media (min-width: 480px) {
  content-container {
    max-width: 480px;
    margin-left: auto;
    margin-right: auto;
    padding-left: 2rem;
    padding-right: 2rem;
  }
}

@media (min-width: 768px) {
  content-container {
    max-width: 768px;
  }
}

nav-bar-container {
  background: #000000;
  position: fixed;
  bottom: 0;
  margin: 0;
  z-index: 1;
  width: 100%;
  height: 55px;
}

nav-bar {
  display: flex;
  position: relative;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  width: 100%;
  height: 100%;
}

nav-bar-link {
  display: flex;
  position: relative;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
  margin: 0;
}

nav-bar-link a {
  display: block;
  text-decoration: none;
  text-align: center;
  color: #ffffff;
  line-height: 55px;
  margin: 0;
  height: 100%;
  width: 100%;
}

nav-bar-link:first-child a {
  padding-left: 2em;
  text-align: left;
}

nav-bar-link:last-child a {
  padding-right: 2em;
  text-align: right;
}

nav-text {
  display: inline;
  font-weight: 300;
}

.nav-current::before,
nav-bar-link:hover nav-text::before {
  position: absolute;
  transform: translateX(-16px);
  font-size: 2rem;
  content: "\B7";
}

.nav-icon {
  display: none;
}

@media (min-width: 480px) {
  nav-bar {
    max-width: 480px;
    margin-left: auto;
    margin-right: auto;
  }
}

@media (min-width: 768px) {
  nav-bar-container {
    background: #ffffff;
    box-shadow: 0 0 6px #999999;
    top: 0;
  }

  nav-bar a {
    color: #000000;
  }

  nav-bar {
    max-width: 768px;
    justify-content: flex-end;
  }

  nav-bar-link {
    width: 120px;
  }

  .nav-icon {
    display: flex;
    position: absolute;
    justify-content: center;
    align-items: center;
    left: 2rem;
    background: var(--red);
    color: #ffffff !important;
    height: 100%;
    width: 55px;
    margin: 0;
    padding-left: 12px;
    padding-right: 12px;
  }

  .nav-icon > * {
    height: 100%;
    width: 100%;
  }
}

home-main {
  display: block;
  margin-bottom: 3.5em;
}

home-main header-top,
home-main header-bottom {
  font-weight: 600;
  margin-top: 1rem;
}

home-main header-top {
  font-size: 2rem;
  margin-bottom: 0.4rem;
}

home-main header-bottom {
  text-align: center;
  background: var(--red);
  color: #ffffff;
  font-size: 2rem;
  letter-spacing: 0.1rem;
  padding-top: 0.3rem;
  padding-bottom: 0.3rem;
  margin-bottom: 2.2rem;
}

home-quote:not(:first-of-type) {
  margin-top: 3rem;
}

social-container {
  display: flex;
  margin-top: 0;
}

social-container a {
  margin-left: 15px;
  margin-right: 15px;
  margin-top: 5px;
  height: 30px;
  width: 30px;
}

social-container a:first-of-type {
  margin-left: 0;
}

social-container a > * {
  margin: 0;
  height: 100%;
  width: 100%;
}

@media (min-width: 768px) {
  home-main {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  home-main-title {
    width: 50%;
    padding-right: 50px;
    transform: translateY(-100px);
  }

  home-main-image {
    width: 50%;
  }

  home-quote-container {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
  }

  home-quote-container > * {
    margin-top: 0 !important;
  }

  home-quote:first-of-type {
    padding-right: 15px;
  }

  home-quote:last-of-type {
    padding-left: 15px;
  }
}

card-content {
  margin-top: 0;
}

card-image {
  display: none;
}

card-title a {
  display: inline-flex;
  padding: 2px 5px;
  margin-top: 1rem;
  margin-bottom: 0.3rem;
  text-decoration: none;
  background: #ffe3e3;
  align-items: center;
}

card-title a:hover {
  background: #fad2d2;
}

@media (min-width: 768px) {
  card-container {
    display: grid;
    grid-template-columns: 250px 1fr;
    grid-gap: 30px;
    align-items: start;
  }

  card-container:first-of-type {
    margin-top: 0 !important;
  }

  card-image {
    display: inline-block;
    overflow: hidden;
  }

  card-text a {
    margin-top: 0;
  }
}



